*, *::before, *::after {
  box-sizing: border-box; }

html {
  font-size: 62.5%; }

body {
  font-size: 1.6rem;
  font-family: "Noto Sans JP", sans-serif;
  background-color: #FFF; }

p {
  text-align: justify; }

button {
  border: none;
  outline: none;
  cursor: pointer; }

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto; }

.no-select {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.right-bottom-radius {
  position: relative;
  border-bottom: 12px solid #FFF;
  border-right: 12px solid #FFF;
  border-bottom-right-radius: 3rem; }
  .right-bottom-radius::after {
    content: "";
    position: absolute;
    bottom: -12px;
    right: -12px;
    border: 15px solid #FFF;
    border-color: transparent #FFF #FFF transparent; }

.right-top-radius {
  position: relative;
  border-top: 12px solid #FFF;
  border-right: 12px solid #FFF;
  border-top-right-radius: 3rem; }
  .right-top-radius::after {
    content: "";
    position: absolute;
    top: -12px;
    right: -12px;
    border: 15px solid #FFF;
    border-color: #FFF #FFF transparent transparent; }

.right-radius {
  border-right: 12px solid #FFF; }

body {
  display: grid;
  grid-template-columns: 2fr 10fr;
  height: 100vh; }

.sidebar {
  display: grid;
  background: linear-gradient(0deg, #259dff 0%, #3a39ff 100%);
  grid-template-rows: repeat(3, minmax(min-content, max-content)) 1fr; }
  .sidebar .logo-container {
    display: flex;
    width: 100%;
    justify-content: center;
    padding-top: 4.8rem;
    padding-bottom: 9.6rem; }
    .sidebar .logo-container .logo {
      font-size: 6.4rem;
      color: #FFF; }
  .sidebar .sidebar-item {
    display: flex;
    align-items: center;
    color: #cccccc;
    padding: 1.6rem 2.4rem;
    width: 100%;
    font-size: 1.4rem;
    font-weight: 100;
    text-align: justify; }
    .sidebar .sidebar-item .sidebar-item-icon {
      margin-right: 1.6rem; }
    .sidebar .sidebar-item:hover {
      color: #FFF;
      font-weight: 400; }
  .sidebar .sidebar-item.selected {
    background-color: #FFF;
    color: #24292e;
    font-weight: 700;
    padding-top: 0.4rem;
    padding-bottom: 0.4rem;
    padding-right: 0; }
    .sidebar .sidebar-item.selected .sidebar-item-icon {
      color: blue; }
  .sidebar button {
    background-color: transparent; }
